import React, { useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { FormGroup, Label, Button, Input } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import { Formik, Field, Form, FastField } from 'formik'
import * as Yup from 'yup'
import BaseFormGroup from '../../components/BaseFormGroup'

const formSchema = Yup.object().shape({
  password: Yup.string().required(
    <FormattedMessage id='createPassword.password.required' />
  ),
  enterThePassword: Yup.string().required(
    <FormattedMessage id='createPassword.enterThePassword.required' />
  )
})

const CreatePassword = ({ isLanding2 }) => {
  const onSubmit = (values, actions) => {
    setTimeout(() => {
      alert(JSON.stringify(values, null, 2))
      actions.setSubmitting(false)
    }, 1000)
  }

  return (
    <Formik
      initialValues={{
        password: '',
        repeatePassword: ''
      }}
      onSubmit={onSubmit}
      validationSchema={formSchema}
    >
      {({ errors, touched }) => (
        <Form className=''>
          <div className='text-center mb-3'>
            <h4 className="font-weight-bold">
              <FormattedMessage id='createPassword.title' />
            </h4>
          </div>
          <BaseFormGroup
            messageId='login.password'
            fieldName='password'
            errors={errors}
            touched={touched}
          />
          <BaseFormGroup
            messageId='createPassword.enterThePassword'
            fieldName='repeatePassword'
            errors={errors}
            touched={touched}
          />
          <div>
            <FormattedMessage id='createPassword.condition.1' />
          </div>
          <div>
            <FormattedMessage id='createPassword.condition.2' />
          </div>
          <div>
            <FormattedMessage id='createPassword.condition.3' />
          </div>

          <div className='d-flex justify-content-center mt-2'>
            <Button color='primary' type='submit'>
              <FormattedMessage
                id={
                  isLanding2
                    ? 'createPassword.continutes'
                    : 'createPassword.done'
                }
              />
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  )
}

export default CreatePassword
